<template>
  <div class="other-info">
    <h6 class="title">2. 其他信息</h6>
    <div class="info-message">
      <div class="message-list">
        <div v-for="(item, index) in normalList" :key="index" class="item">
          <span class="label">{{ item.componentBO.tagName }}：</span>
          <span class="value">
            {{ item.componentBO.value }}
            <el-link
              v-if="['business_label'].includes(item.componentBO.propertyKey)"
              @click="handleEditBusinessLabel(item, index)"
              type="primary"
              :underline="false"
              >修改</el-link
            >
          </span>
        </div>
      </div>
      <div class="file-list">
        <div v-for="(item, index) in fileList" :key="index" class="item">
          <div class="item-content">
            <div
              v-for="(fileItem, fileIndex) in item.componentBO.value"
              :key="fileIndex"
              class="file-item"
            >
              <template v-if="pictureExtensionList.includes(fileItem.type)">
                <img
                  @click="
                    previewImage(
                      item.componentBO.tagName,
                      item.componentBO.value,
                      index
                    )
                  "
                  :src="fileItem.url"
                  alt=""
                />
              </template>
              <template v-if="textExtensionList.includes(fileItem.type)">
                <el-link
                  type="primary"
                  :underline="false"
                  :icon="Paperclip"
                  @click="downLoadFile(fileItem.url)"
                  >点击下载</el-link
                >
              </template>
            </div>
          </div>
          <div class="label">{{ item.componentBO.tagName }}</div>
        </div>
      </div>
    </div>
  </div>
  <EditBusinessLabelDialog
    ref="editBusinessLabelRef"
    @emitFunc="updateBusinessLabel"
  ></EditBusinessLabelDialog>
</template>

<script setup lang="ts">
import { ref, computed, shallowReactive, defineAsyncComponent } from "vue";
import { Paperclip } from "@element-plus/icons-vue";

import { downLoadFile } from "jz-tool-lib";
import { fileExtensionList } from "@/assets/common";

const EditBusinessLabelDialog = defineAsyncComponent(
  () => import("./edit-business-label-dialog.vue")
);
const props = withDefaults(
  defineProps<{
    otherInfo: {
      normalList: any[];
      fileList: any[];
    };
  }>(),
  {
    otherInfo: () => {
      return {
        normalList: [],
        fileList: [],
      };
    },
  }
);

const emits = defineEmits<{
  (e: "emitFunc", value: { title: string; imgList: string[]; index: number });
}>();

const editBusinessLabelRef = ref();
const normalList = computed(() => props.otherInfo.normalList);
const fileList = computed(() => props.otherInfo.fileList);
const textExtensionList = fileExtensionList("text");
const pictureExtensionList = fileExtensionList("picture");
const initData = shallowReactive({
  businessLabelIndex: -1,
});

function previewImage(title: string, imgList: string[], index: number) {
  const list = imgList.map((item: any) => item?.url);
  emits("emitFunc", {
    title,
    imgList: list,
    index,
  });
}
// 修改业务标签
function handleEditBusinessLabel(item, index) {
  initData.businessLabelIndex = index;
  const list = item.value ? JSON.parse(item.value) : [];
  const labelList = list.reduce((arr, item) => {
    const labels = item.labels.map((sonItem) => {
      sonItem.id = `${sonItem.id}&${item.group.id}`;
      return sonItem;
    });
    return arr.concat(labels);
  }, []);
  const labelValue = labelList.map((item) => item.id);
  const groupLabelValue = list.map((item) => item.group.id);
  editBusinessLabelRef.value.trigger(true, {
    labelValue,
    groupLabelValue,
  });
}
// 更新业务标签
function updateBusinessLabel(value) {
  const labelList = JSON.parse(value);
  const info = normalList.value[initData.businessLabelIndex];
  info.value = value;
  info.componentBO.value = labelList
    .map((groupItem) => {
      const groupStr = groupItem?.group.labelGroupName;
      const labelStr = groupItem.labels
        .map((labelItem) => {
          return labelItem.labelName;
        })
        .toString();
      return `业务分类：${groupStr}；标签：${labelStr}`;
    })
    .toString();
}
</script>

<style scoped lang="scss">
.other-info {
  > .title {
    margin-bottom: 40px;
  }

  .message-list {
    display: flex;
    flex-wrap: wrap;
    padding: 14px 0;

    .item {
      width: 33.33%;
      padding-right: 10px;
      margin-bottom: 10px;
      text-align: justify;
    }
    .label {
      font-size: 14px;
      font-weight: 400;
      color: #9ca6b9;
      line-height: 18px;
    }

    .value {
      font-size: 14px;
      font-weight: 400;
      color: #242934;
      line-height: 18px;
      .el-link {
        margin-left: 5px;
        vertical-align: baseline;
      }
    }
  }

  .file-list {
    display: flex;
    flex-wrap: wrap;
    padding: 14px 0;

    .item {
      @include flex(initial, flex-end, column);
      padding: 14px 0;
      margin: 0 24px;
    }

    .label {
      font-size: 14px;
      font-weight: 400;
      color: #242934;
      line-height: 14px;
      text-align: center;
      margin-top: 20px;
    }
    .item-content {
      display: flex;
      flex-wrap: wrap;
    }
    .image-list {
      display: flex;
      flex-wrap: wrap;
      border-radius: 4px;
      overflow: hidden;
    }

    img {
      width: 168px;
      height: 118px;
      object-fit: contain;
      margin-right: 20px;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}
</style>
